import React, { useRef } from 'react'
import { Grid, Typography } from '@material-ui/core'

// Components
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer'
import PageFooter from 'Pages/Protected/BillingAndCollection/Components/PageFooter/PageFooter'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BillingSOAReportService from 'Services/Api/Billing/SOA/BillingSOAReportService'
import CollectionLookupService from 'Services/Api/Collection/LookupServices'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { useHistory } from 'react-router-dom'

// style
import classes from './style.module.scss'

// Security
import RBAC from 'Security/RBAC'

const moment = require('moment')

export default function SOAReport (props) {
  const { soa_number = '' } = props.match.params
  const [pageData, setPageData] =  React.useState({})
  const [file, setFile] = React.useState()
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Statement (BIR)', 'Statement (Corporate)', 'Attachment'],
    category: ['collection-preview', 'category', 'list'],
    stepNumber: 1,
    stepCount: 4
  })
  const child = React.useRef()
  const history = useHistory()
  const refContainer = useRef()

  React.useEffect(() => {
    CollectionLookupService.getAccountBySOANumber({ soa_number })
      .then(res => {
        const { data = { data: {} } } = res
        setPageData(i => ({ ...i, ...data.data }))
      })
      .catch(err => console.error(`Error Fetching Data: ${err}`))
    handleFetchReport()
    handlePageData()
  }, [])

  React.useEffect(() => { handlePageData(); }, [pageData.account_name])
  React.useEffect(() => { setFile(); handleFetchReport() }, [stepper.stepNumber])

  const handlePageData = () => {
    PageTitleService()
    console.log(pageData)
    BreadcrumbsService.setSelectedItemsWithoutHome([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `ledgerId:${pageData.account_name || ''}`
    ])
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handleFetchReport = () => {
    BillingSOAReportService.getReport({
      type: stepper.category[stepper.stepNumber - 1 || 0] || 'preview',
      soaNumber: soa_number
    })
      .then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
      })
      .catch(err => console.error(`Error fetching Data: ${err}`))
    refContainer.current.parentElement.scrollTo(0, 0)
  }

  const handleOnDownload = () => {
    if (!file) return false
    const url = URL.createObjectURL(file)
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `${soa_number}-${moment().format('L')}.pdf`)
    document.body.appendChild(link)
    link.click()
  }

  return (
    <Grid container direction='column' className={classes.root} ref={refContainer}>
      <Grid item>
        <Grid container direction='row'>
          <Grid item xs={8}>
            <Typography id='generate-reports-header' variant='h5' className={classes.bold}>
              {pageData.account_name || ''} - Statement of Account
            </Typography>
          </Grid>
          <Grid item xs={4}>
            <FormStepper {...stepper} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item>
        <Grid container direction='row' justify='space-between'>
          <Grid item>
            <Grid container direction='column'>
              <Grid item><Typography className={classes.mute}>ACCOUNT NO.: {pageData.account_number || ''}</Typography></Grid>
              <Grid item>
                <Typography className={classes.mute}>
                  PERIOD COVERED: {pageData.period_start ? moment(pageData.period_start).format('ll') : ''} - {pageData.period_end ? moment(pageData.period_end).format('ll') : ''}
                </Typography>
              </Grid>
              <Grid item><Typography className={classes.mute}>PAYMENT TERM: {pageData.payment_term || ''}</Typography></Grid>
            </Grid>
          </Grid>
          <Grid item>
            <Grid container direction='column' justify='space-around' className={classes['soa-details-wrapper']}>
              <Grid item>
                <Grid container direction='row'>
                  <Grid item xs={12}>
                    <Grid container direction='row'>
                      <Grid item xs={3}><Typography className={`${classes.bold} ${classes.details}`}>SOA NO.:</Typography></Grid>
                      <Grid item xs><Typography className={`${classes.bold} ${classes.details}`}>{pageData.soa_number || ''}</Typography></Grid>
                    </Grid>
                  </Grid>
                  <Grid item xs={12}>
                    <Grid container direction='row'>
                      <Grid item xs={3}><Typography className={`${classes.bold} ${classes.details}`}>ATP NO.:</Typography></Grid>
                      <Grid item xs><Typography className={`${classes.bold} ${classes.details}`}>{pageData.atp_number || ''}</Typography></Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item>&nbsp;</Grid>
      <Grid item>
        <PdfViewer
          initialScale={1}
          file={file}
          mountedRef={child}
          download={e => handleOnDownload()}
        />
      </Grid>
      <Grid item xs={12}>
        <PageFooter
          handleOnCloseClicked={() => {
            history.goBack()
          }}
          handleOnCancelClicked={() => history.goBack()}
          handleStepChange={e => handleStepChange(e)}
          isView
          isVoid={false}
          stepNumber={stepper.stepNumber}
          stepCount={stepper.stepCount}
        />
      </Grid>
    </Grid>
  )
}
