import instance from '../../../instance'
const axios = instance('/b/a/j')

export default class JournalAccountsServices {
  static getListOfAccounts (params) {
    return axios({
      url: `/billing/account/journals`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getJournalTransactions (params) {
    return axios({
      url: `/billing/client/journal/transactions`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
}
