import Grid from '@material-ui/core/Grid'
import CloseIcon from '@material-ui/icons/Close'
import React from 'react'
import { Button, Dialog, Typography } from '@material-ui/core'

// Styles
import classes from './ActionDialog.module.scss'

export const SuccessDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnClose = defaultFunction, handleOnOk = defaultFunction } = props

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-assign-success']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Adhoc Bill Run</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>
            The Pre-SOA has been successfully generated.
          </Typography>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>
            The Billing Analyst of the Account shall receive and see the generated Pre-SOA in the Clients Pre-SOA to Analyze tile
          </Typography>
        </Grid>
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='center'>
            <Grid item xs={5}>
              <Button id='submitted-confirm-button' className={classes.confirm} variant='contained' onClick={() => handleOnOk()}>
                OK
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}
