import React from 'react'
import Chart from 'react-apexcharts'
import { Grid, Typography } from '@material-ui/core'

// Tile
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Services
import CollectionService from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'

// Style
import classes from './style.module.scss'

const numeral = require('numeral')

const ActualReportTile = props => {
  const history = useHistory()
  const [tile, setTile] = React.useState({
    filterMenu: {
      data: [
        { value: 'previous-month', text: 'Previous Month' },
        { value: 'current-month', text: 'This Month' },
        { value: 'next-month', text: 'Next Month' }
      ],
      default: 'current-month'
    },
    user_owned: true,
    results: []
  })

  React.useEffect(() => {
    Promise.all([
      CollectionService.getTargetActualReport({
        range: tile.filterMenu.default
      })
    ]).then(res => {
      const [CDFService] = res
      const { results, title } = CDFService.data.data
      setTile(i => ({ ...i, results, title }))
    }).catch(err => console.error(`Error fetching Reports: ${err}`))

  }, [tile.filterMenu.default])

  return (
    <React.Fragment>
      <DashboardTile
        id='target-vs-actual-tile'
        sizes={{ sm: 12 }}
        title=''
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        headerSize={'h6'}
        countSize={'h5'}
        titleOverride={(
          <Grid container direction='column'>
            <Grid item>
              <Typography variant='h5' className={classes.title}>Target versus Actual Report</Typography>
            </Grid>
            <Grid item>
              <Typography variant='h6'>{tile.title || ''}</Typography>
            </Grid>
          </Grid>
        )}
      >
        <div className={classes.root} onClick={() => { history.push('/reports') }}>
          <Chart
            type='bar'
            width='100%'
            height='100%'
            options={{
              chart: {
                toolbar: {
                  show: false
                },
                stacked: false
              },
              stroke: {
                curve: 'smooth',
                width: 0,

              },
              fill: {
                opacity: [1, 0.25],
                gradient: {
                  inverseColors: false,
                  shade: 'light',
                  type: "vertical",
                  opacityFrom: 0.85,
                  opacityTo: 0.55,
                  stops: [0, 100, 100, 100]
                }
              },
              legend: {
                show: true,
                position: 'top',
                horizontalAlign: 'right',
                showForSingleSeries: true,
                showForZeroSeries: true
              },
              tooltip: {
                shared: true,
                intersect: false
              },
              dataLabels: { enabled: false },
              xaxis: { categories: tile.results.map(i => { return i.name }), labels: { show: !!(tile.results.length > 0) } },
              yaxis: { show: !!(tile.results.length > 0), labels: { formatter: (value) => { return (numeral(value).format('0 a').toUpperCase() ) } } }
            }}
            series={[
              { name: 'Actual', type: 'column', data: tile.results.map(i => { return i.payment }) },
              { name: 'Target', type: 'area', data: tile.results.map(i => { return i.amount }) }
            ]}
          />
        </div>
      </DashboardTile>
    </React.Fragment>
  )
}

ActualReportTile.displayName = 'DashboardTileComponent'

export default ActualReportTile
