import RBAC from 'Security/RBAC'

import classes from './InvoiceSubmittedForApprovalPage.module.scss'

const tableHeaders = [
  {
    id: 'pending-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Billing Code & Type *',
    center: true
    // search: true,
    // sort: true,
    // accessor: 'computed_fees.billing_code'
  },
  {
    id: 'pending-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Member ID',
    center: true
    // search: true,
    // sort: true,
    // accessor: 'member.id'
  },
  {
    id: 'pending-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Member Name',
    center: true
    // search: true,
    // sort: true,
    // accessor: 'member.first_name'
  },
  {
    id: 'pending-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Description *',
    center: true
    // search: true,
    // sort: true,
    // accessor: 'computed_fees.description'
  },
  {
    id: 'pending-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Taxable Amount *',
    center: true
    // search: true,
    // sort: true,
    // accessor: 'computed_fees.value|isNumber'
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: '',
    center: true
  }
]

export default function InvoiceToBeRevised (props = {}) {
  PageTitleService()
  const history = useHistory()

  const transaction_number = props.match.params.transaction_number
  // const type = props.match.params.type

  const [pendingInvoices, setPendingInvoices] = React.useState({
    data: []
  })

  // const [disabledSave, setDisabledSave] = React.useState(false)
  console.log(transaction_number)
  const [invoiceDetail, setInvoiceDetail] = React.useState({})
  React.useEffect(() => {
    Promise.all([BillingClientInvoicesServices.getTobeRevisedInvoice({ transaction_number: transaction_number })]).then(values => {
      console.log(values)
      setPendingInvoices(prevState => ({
        ...prevState,
        data: values[0].data.data
      }))
    })
  }, [])

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    // order: true,
    // orderBy: '_id',
    orderBy: 'date.added',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  // Initialize
  React.useEffect(() => {
    fetchData({})
    BreadcrumbsService.setSelectedItems(['dashboard', 'forApprovalInvoice', `forApprovedInvoicalTransNumber:${transaction_number}`])
  }, [])

  const fetchData = (params) => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)

    InvoiceViewServices.getInvoiceDetails(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    })
  }

  const transactionInfo = pendingInvoices.data
  const clientInfo = pendingInvoices.data.client
  const billingInfo = pendingInvoices.data.billing
  const contractInfo = pendingInvoices.data.contract
  const summaryInfo = pendingInvoices.data.summary
  const dateInfo = pendingInvoices.data.date
  // const invoiceInfo = invoiceDetail.data
  function handleClose () {
    history.push('/')
  }

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const changeView = params => {
    fetchData(params)
  }
  // For Modify Bill Run inclusion Popper
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [billInclusion, setBillInclusion] = React.useState(true)
  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
      setTempValue(value)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'transitions-popper' : undefined
  const [value, setValue] = React.useState('Include in the upcoming bill run')
  const [tempValue, setTempValue] = React.useState('Include in the upcoming bill run')
  const [errors, setErrors] = React.useState({})
  const handleChange = (event) => {
    setValue(event.target.value)
  }
  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-corporate-name' variant='h5' style={{ fontWeight: 'bold' }}>
          {clientInfo && clientInfo.account_name} - Invoice
        </Typography>
        <Typography id='header-details-account-number' color='textSecondary' style={{ fontWeight: 'bold' }}>
          ACCOUNT NUMBER {clientInfo && clientInfo.account_number}
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo && clientInfo.account_name}</Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value' style={{ fontWeight: 'bold' }}>{clientInfo && clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value' style={{ fontWeight: 'bold' }}>{clientInfo && clientInfo.account_status}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value' style={{ fontWeight: 'bold' }}>{contractInfo && contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value' style={{ fontWeight: 'bold' }}>{clientInfo && clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography id='drawer-tax-type-value' style={{ fontWeight: 'bold' }}>{clientInfo && clientInfo.tin}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value' style={{ fontWeight: 'bold' }}>
                {billingInfo && billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value' style={{ fontWeight: 'bold' }}>{contractInfo && contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value' style={{ fontWeight: 'bold' }}>
                {moment(contractInfo && contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value' style={{ fontWeight: 'bold' }}>
                {moment(contractInfo && contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value' style={{ fontWeight: 'bold' }}>{clientInfo && clientInfo.location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>

            {RBAC.hasPolicy({ or: ['BC-042'] }) && (
              <Link
                id='export-link'
                onClick={() => handleExportInvoiceTransaction({
                  list: list,
                  tableHeaders: tableHeaders,
                  transactionNumber: transaction_number,
                  title: 'Submitted Invoice Transaction'
                })}
                className={classes['export-link']}
              >
                <ReplyIcon id='export-icon' className={classes['export-icon']} />
                Export
              </Link>
            )}

            <Link id='view-billing-link' aria-describedby={id} onClick={handleClick} className={classes['view-link']}>
              <FontAwesomeIcon id='view-billing-icon' className={classes['view-icon']} icon={faEdit} />
              Modify Bill Run Inclusion
            </Link>
            <Popper style={{'z-index': '3000'}} id={id} open={open} anchorEl={anchorEl} transition>
              {({ TransitionProps }) => (
                <Fade {...TransitionProps} timeout={350}>
                  <div
                    style={{
                      padding: '10px',
                      'background-color': 'white',
                      'box-shadow': '5px 5px rgb(0,0,0,10%)'

                    }}
                  >
                    <div>
                      <span>Select Bill Run inlcusion for this transaction<span style={{'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer'}}
                        onClick={e => {
                          handleClick(e)
                          setValue(tempValue)
                        }}>X</span></span>
                    </div>
                    <div>
                      <RadioGroup aria-label='gender' name='gender1' value={value} onChange={handleChange}>
                        <FormControlLabel value='Include in the upcoming bill run' control={<Radio color='primary' />} label={<span style={{ fontSize: '14px' }}>Include in the upcoming bill run</span>} />
                        <FormControlLabel value='Postpone to the next bill run' control={<Radio color='primary' />} label={<span style={{ fontSize: '14px' }}>Postpone to the next bill run</span>} />
                      </RadioGroup>
                    </div>
                    <div>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        onClick={e => {
                          handleClick(e) // close modify bill run inclusion pop up
                          setValue(tempValue)
                        }}
                        style={{color: '#3ab77d',
                          'background-color': 'white',
                          width: '100px',
                          'margin-left': '100px',
                          textTransform: 'none'
                        }}
                      >
                        Cancel
                      </Button>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        style={{color: 'white',
                          'background-color': '#3ab77d',
                          width: '100px',
                          'margin-left': '10px',
                          textTransform: 'none'
                        }}
                        onClick={(e) => {
                          if (value === 'Include in the upcoming bill run') {
                            setBillInclusion(true)
                            setTempValue(value)
                            setValue('Include in the upcoming bill run')
                            setBillInclusion(true)
                          } else {
                            setBillInclusion(false)
                            setValue('Postpone to the next bill run')
                            setTempValue(value)
                            setBillInclusion(false)
                          }
                          handleClick(e) // close modify bill run inclusion pop up
                        }}
                      >
                        Apply
                      </Button>
                    </div>
                  </div>
                </Fade>
              )}
            </Popper>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                PERIOD COVERED
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                REFERENCE NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {transaction_number}
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(dateInfo && dateInfo.created).format('MMM DD, YYYY')}
              </Typography>
              <Typography
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              >
                {transactionInfo.transaction && transactionInfo.transaction.period.start ? moment(transactionInfo.transaction.period.start).format('MMM DD, YYYY') : ' '}{' '}
                - {transactionInfo.transaction && transactionInfo.transaction.period.end ? moment(transactionInfo.transaction.period.end).format('MMM DD, YYYY') : ' '}
              </Typography>
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable
          count={list.count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {list &&
              list.body.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  />
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>
          <div className={classes['item-right-side']}>
            <Button
              id='close-button'
              variant='contained'
              color='primary'
              className={classes['button-positive']}
              onClick={e => handleClose()}
              style={{ textTransform: 'none' }}
            >
              Close
            </Button>
          </div>
        </div>
      </FormDrawer>
    </div>
  )
}
