/*  Hooks */
import clsx from 'clsx'
import React from 'react'

/* Material UI Components */
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import CloseIcon from '@material-ui/icons/Close'
import IconButton from '@material-ui/core/IconButton'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

/* Fonts */
import Typography from '@material-ui/core/Typography'

/* Styles */
import classes from './CustomizedDialogs.module.scss'

const SuccessDialog = props => {
  const { page, openSuccessDialog, setOpenSuccessDialog, submitFunction, resetDialogPage } = props

  const handleOnClose = () => {
    setOpenSuccessDialog(false)
  }

  const handleOnSuccess = () => {
    submitFunction(false)
    resetDialogPage()
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '320px', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <Typography id={`${page}-title`} className={classes.title}>
            {page === 'update'
              ? 'Tax Charging Update Saved Successfully'
              : ''}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent className={classes['dialog-content']}>
        <br />
        {page === 'update'
          ? 'The change/updates made to the selected Tax Charging has been successfully updated.'
          : ''}
      </DialogContent>
      <DialogActions>
        <Grid container>
          <Grid item className={classes.leftGrid}>
            {''}
          </Grid>
          <Grid item className={clsx(classes.rightGridSuccess, classes.alignEnd)}>
            <Button
              variant='contained'
              id={`${page}-ok-button`}
              className={classes['button-positive']}
              onClick={handleOnSuccess}
            >
              OK
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { page, openDialog, setOpenDialog, submitFunction, itemStatus, resetDialogPage } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  const handleOnClose = () => {
    setOpenDialog(false)
  }

  const onShowSuccessDialog = e => {
    if (page === 'update') {
      if (openDialog === true) {
        setOpenSuccessDialog(true)
        if (openSuccessDialog === true) setOpenDialog(false)
      }
    } else {
      if (e) setOpenDialog(false)
    }
    submitFunction()
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '320px', display: 'flex' } }}
      >
        <React.Fragment>
          <DialogTitle disableTypography className={classes['header-container']}>
            <div className={classes.header}>
              <IconButton
                aria-label='close'
                id='confirm-close-button'
                className={classes.button}
                onClick={handleOnClose}
              >
                <CloseIcon />
              </IconButton>
              <Typography id='confirm-title' className={classes.title}>
                {page === 'update' ? 'Save Tax Charging Updates' : `${itemStatus ? 'Activate' : 'Deactivate'} Tax Charging`}
              </Typography>
            </div>
          </DialogTitle>
          <DialogContent className={classes['dialog-content']}>
            {page === 'update' ? 'Are you sure you want to save the updates made to the Tax Charging entry?' : `Are you sure you want to ${itemStatus ? 'activate' : 'deactivate'} the selected Tax Charging?`}
            <br />
            <br />
            {page === 'update' ? 'Changes made to the Tax Charging will only be applied to future transactions after the update.' : "Please ensure that you have informed the Administrator that you'll will be deactivating this Tax Charging. Once deactivated, it will no longer be available for selection in the future transactions."}
            {page === 'update' ? (<br />, ' Do you still want to continue?') : ''}
          </DialogContent>
          <DialogActions>
            <Grid container>
              <Grid item className={classes.leftGrid}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleOnClose}
                >
                  No
                </Button>
              </Grid>
              <Grid item className={clsx(classes.rightGrid, classes.alignEnd)}>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={e => onShowSuccessDialog(e)}
                >
                  Yes
                </Button>
              </Grid>
            </Grid>
          </DialogActions>
        </React.Fragment>
      </Dialog>
      <SuccessDialog
        page={page}
        submitFunction={setOpenDialog}
        openSuccessDialog={openSuccessDialog}
        setOpenSuccessDialog={setOpenSuccessDialog}
        resetDialogPage={resetDialogPage}
      />
    </React.Fragment>
  )
}
